@use "vars";

.text-thin {
  color: vars.$black;
  font-weight: 300;
  font-size: 1.25rem;
  line-height: 1.5;
  letter-spacing: 0.5px;
}

.btn-round {
  padding: 10px 30px;
  border: 1px solid vars.$black;
  border-radius: 50px;
  font-weight: 300;
  letter-spacing: 0.5px;
  color: vars.$black;
  cursor: pointer;
  transition: all 200ms ease-in-out;

  &:hover,
  &:active {
    background-color: vars.$neon-green;
    color: vars.$white;
    border-color: vars.$neon-green;
  }
}

.link-empty {
  color: vars.$black;
  cursor: pointer;
  transition: all 200ms ease-in-out;

  &:hover,
  &:active {
    color: vars.$neon-green;
  }
}
.link-empty-rev {
  color: vars.$neon-green;
  cursor: pointer;
  transition: all 200ms ease-in-out;

  &:hover,
  &:active {
    color: vars.$black;
  }
}

.regular-par {
  font-size: 3rem;
  padding: 0;
  margin: 0;
}

.right-arr {
  transition: all 100ms ease-in-out;
  cursor: pointer;

  &:hover {
    transform: translateX(5px);
  }
}
.left-arr {
  transition: all 100ms ease-in-out;
  cursor: pointer;

  &:hover {
    transform: translateX(-5px);
  }
}
